	module.exports = {"data":{"markdownRemark":{"html":"<p>早朝の「醜いレーン」を読むことは、不思議な本です。色で書かれた物語は深刻ではないようです。物語は比較的平坦であり、社会的に大規模で集中的な意味はありません。</p>\n<p>朝の朝会議を開きます。病院設立の基本方針が提案されており、基本的な方針として、さまざまな部署に取引権限を委譲し、部署の熱意を十分に発揮させることが挙げられます。グループでは、多くの面で常に熱意を持っていなければなりません。さもなければ活力と創造性は十分ではありません。しかし、どの程度まで電力が分解されるかは、各コンポーネントの品質に関連しています。各部品の品質が高いと、電力を大きく分解することができます。各部品の品質が低い場合は、分解するのが困難です。システムと同じくらい大きい国と同じくらい大きいので、一般原則は同じです。</p>\n<p>また、研究所の開発戦略を立案するチームを立ち上げる準備をしています。これは大学の精神的なものです。精神的なことだけが立ち上がり、大学全体が立ち上がることができます。</p>\n<p>Lに返信するこの手紙では、私はまだ「時計を壊している」と言って、私たちは学び、欲望がなく、安心している修道士のようです。 Lは数年前に私に尋ねました：「あなたは毎日理にかなっていますか？」私は「僧侶がなぜ詠唱すべきかを知っていますか？」と私は手紙でこう言いました。「私の心の中で誰かが私の幸福を考えるのはまれです。私たちのような人たちは、幸福のない人生、つまり鈍い人生に慣れてきました。幸福の欲求がなければ、痛みはありません。痛みはしばしば幸福の追求を伴います。私の質問は「なぜ僧侶は聖典を朗読したいのですか」という疑問は答えられると思いますが、すべてが平易さに起因するものであり、無限の機会を生むために平和と呼ばれています幸福、何年も練習した後、心の中に十分な空間があり、幸福はないのです。もちろん、他の人の幸福追求に反対しているわけではありません。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0155.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0155.md","frontmatter":{"title":"10月8日 土曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0155.md absPath of file >>> MarkdownRemark"}}

